.friend-list {
  padding: 0;
  margin: 0;
  list-style: none;
  max-width: 1170px;
  display: flex;
  margin: -10px auto;
}

.friend-item {
  display: inline-block;
  padding: 7px 20px;
  margin-bottom: 10px;
  border-radius: 5px;
  border-left: 10px solid #f05d22; 
  box-shadow: 2px -2px 5px 0 rgba(0,0,0,.1),
     -2px -2px 5px 0 rgba(0,0,0,.1),
    2px 2px 5px 0 rgba(0,0,0,.1),
    -2px 2px 5px 0 rgba(0,0,0,.1);
  font-size: 20px;
  letter-spacing: 2px;
  transition: 0.3s all linear;
}
.friend-list .friend-item + .friend-item {
  margin-left: 50px;
}
.friend-item:hover {
     border-left: 10px solid green; 
}